<template>
    <SelectButton
        v-model="selectedFilter"
        :options="filters"
        optionLabel="name"
        optionValue="path"
        @change="setFilter"
    />
</template>

<script>
import SelectButton from 'primevue/selectbutton';

export default {
    name: 'DocContent',
    components: {
        SelectButton
    },
    props: {
        filter: String
    },
    data() {
        return {
            selectedFilter: this.$route.params.filter,
            filters: [
                { name: "",      path: 'posting' },
                { name: "",      path: 'store'   },
                { name: "",         path: 'request' },
                { name: "",       path: 'order'   },
                { name: "",           path: 'check'   },
                { name: "", path: 'invent'  },
                { name: "",            path: ''        },
            ],
        };
    },
    watch: {
        filter (val) {
            this.selectedFilter = val;
        }
    },
    methods: {
        setFilter(event) {
            this.$router.push(`/documents/${event.value}`);  
        },
    },
}
</script>
